.container {
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 1rem;
}

.title {
  margin-top: 50px;
  color: red;
  font-size: 48px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 10px;
}

.title span {
  color: blue;
  padding-left: 10px;
}

.title img {
  width: 50px;
  height: 50px;
  padding: 10px;
  object-fit: contain;
}

.reset {
  width: 200px;
  height: 50px;
  background-color: blue;
  color: red;
  font-weight: bold;
  font-size: 24px;
  border: 2px solid red;
  border-radius: 10px;
  cursor: pointer;
  margin-top: 40px;
}

.board {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 40px;
}

.row1, .row2, .row3 {
  display: flex;
  gap: 10px;
}

.boxes {
  width: 120px;
  height: 120px;
  background-color: blue;
  border: 2px solid red;
  border-radius: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: 0;
  overflow: hidden;
  flex-shrink: 0; /* ✅ Prevent shrink/stretch */
}

/* ✅ Fix: keep image size fixed and don’t expand box */
.boxes img {
  width: 70px;
  height: 70px;
  object-fit: contain;
  pointer-events: none;
  display: block;
}
@media (max-width: 600px) {
  .title {
    font-size: 20px;
  }

  .title img {
    width: 30px;
    height: 30px;
    padding: 5px;
  }

  .reset {
    width: 150px;
    height: 40px;
    font-size: 18px;
    margin-top: 30px;
  }

  .board {
    gap: 8px;
    margin-top: 20px;
  }

  .boxes {
    width: 22vw;
    height: 22vw;
    border-radius: 15px;
  }

  .boxes img {
    width: 70%;
    height: 70%;
  }
}
